import { writable } from "svelte/store";

const devRI = [0,0];
function createBasicValue (defValue, name) {
	const { subscribe, update, set } = writable(JSON.parse(localStorage.getItem(name)) 
													 ||defValue);
	subscribe(val => localStorage.setItem( name, JSON.stringify(val) ));
	
	return {
		subscribe,
		update,
		set,
		reset: () => update(waveArr => {
			return Array.isArray(waveArr) ? waveArr.fill(0) : defValue;
		})
	};
}
export const real = createBasicValue(devRI, "real");
export const imag = createBasicValue(devRI, "imag");
export const freq = createBasicValue(440, "freq");
export const cents = createBasicValue(0.1, "cents");
export const volume = createBasicValue(1, "volume");
export const dur = createBasicValue(0.5, "dur");
