function createWavesArr () {
	const defValue = [];
	const { subscribe, set, update } = writable( JSON.parse(localStorage.getItem("wavesArr")) || defValue );
	
	subscribe(val => localStorage.setItem( "wavesArr", JSON.stringify(val) ));
	
	return {
		subscribe,
		saveSound: (nWave, i = null) => update(wavesArr => {
			const { real, imag } = nWave;
			const oWave = wavesArr[i];
			const nnWave = {
				...oWave,
				...nWave,
				real: [...real],
				imag: [...imag]
			};
			if (i === null) {
				wavesArr.push(nnWave);
			} else if (i >= 0) {
				wavesArr.splice(i, 1, nnWave);
			}
			return wavesArr;
		}),
		setName: (name, i) => update(wavesArr => {
			if (i !== null && i >= 0) {
				wavesArr[i].name = name;
			}
			return wavesArr;
		}),
		deleteSound: i => update(wavesArr => {
			if (i !== null && i >= 0) {
				wavesArr.splice(i, 1);
			}
			return wavesArr;
		}),
		reset: () => set(defValue)
	};
}
export const wavesArr = createWavesArr();

