<script>
	// import mexp from 'math-expression-evaluator';
	import {evaluate} from 'mathjs';
	import Formula from '@hapi/formula';
	const functions = {
    x: (value) => value + 10
};

const constants = {
    Z: 100
};

const reference = function (name) {
    
    return (context) => context[name];
};


const formula2 = new Formula.Parser('1 + a.b.c.2.4.x + [b] + x([y + 4] + Z)', { functions, constants, reference });

formula2.evaluate({ 'a.b.c.2.4.x': 2, b: 3, 'y + 4': 5 });       // 1 + 2 + 3 + 5 + 10 + 100
formula2.evaluate({ 'a.b.c.2.4.x': '2', b: 3, 'y + 4': '5' });   // '123510010'
	
	
	export let arr;
	export let title;
	
	let usedFormula;
	let formula;
	
	const handleKeyup = (event) => {
		if (['Enter',"NumpadEnter"].includes(event.code)) {
			usedFormula = formula;
			// var value = de("1/(pi*a)");
			// real.fill(0);
			// real[0] = 0.5;
			for (let i = 1; i < arr.length; i++) { // note i starts at 1
				// arr[i] = mexp.eval(usedFormula,[{type:3,token:"a",value:"a",preced:0}],{a:i}); //1 / (i * Math.PI);//
				arr[i] = evaluate(usedFormula,{a:i});
			}
			// var value = mexp.eval("1/(pi*2)");
			// console.log(value);
		}
	}
</script>

<input id="formula" list="mf" title={title} placeholder={title} class:is-use={formula===usedFormula} bind:value={formula} on:keyup|preventDefault={handleKeyup}>
<datalist id="mf">
	<option value="2/(-pi*a)">
	<option value="1">
</datalist>

<style>
	#formula:not(.is-use) {
		background: red;
	}
</style>
