<script>
	import { onMount } from 'svelte';
	
	// export let ac;
	export let waveform;
	let scopeCanvas;
	let scopeContext;
	export let analyser;
	export let gainNode;
	onMount(()=>{
		scopeContext = scopeCanvas.getContext('2d');
	})
$:draw(),analyser;


// oscilloscope
// let scopeCanvas;
// let scopeContext;

const draw = ()=>{
        // this.gainNode.connect(analyser);
				console.log("draw");
if (!analyser) { return false; }
console.log("draw");
waveform = new Float32Array(analyser.frequencyBinCount);
console.log("analyser.frequencyBinCount",analyser.frequencyBinCount)
analyser.getFloatTimeDomainData(waveform);
gainNode.connect(analyser);
(function updateWaveform() {
    requestAnimationFrame(updateWaveform);
    analyser.getFloatTimeDomainData(waveform);
}());

var spectrum = new Uint8Array(analyser.frequencyBinCount);
console.log("analyser.frequencyBinCount",analyser.frequencyBinCount);
(function updateSpectrum() {
    requestAnimationFrame(updateSpectrum);
    analyser.getByteFrequencyData(spectrum);
}());

scopeCanvas.width = waveform.length;
scopeCanvas.height = scopeCanvas.width * 0.33;
drawOscilloscope(waveform);
	
	(function drawOscilloscopeRAF() {
		if (waveform[0]!=0) {
			// console.log(waveform);
			drawOscilloscope(waveform);
		} else {
			requestAnimationFrame(drawOscilloscopeRAF);
		}
	}());
};
function drawOscilloscope (waveform) {
		scopeContext.clearRect(0, 0, scopeCanvas.width, scopeCanvas.height);
		scopeContext.strokeStyle = "white"; // Green path
		scopeContext.beginPath();
		for (var i = 0; i < waveform.length; i++) {
				var x = i;
				var y = (0.5 + waveform[i] / 2) * scopeCanvas.height;
				if (i === 0) {
						scopeContext.moveTo(x, y);
				} else {
						scopeContext.lineTo(x, y);
				}
		}
		scopeContext.stroke();
}
</script>

<div id="canvas-container">
	<canvas bind:this={scopeCanvas} id="canvas" width="500" height="375"></canvas>
</div>

