<script>
import { bt, btCtrl } from "./event-fns.js"
import { keys } from "./keys.js"

import CanvasWave from "./CanvasWave.svelte";
import AdvancedPanel from "./AdvancedPanel.svelte";
import { onMount } from 'svelte';
import { real, imag, wavesArr } from "./wave.js"
import { volume, dur, freq, cents } from "./wave.js"
const { saveSound, deleteSound, setName } = wavesArr;

import {tick} from "svelte";

$:aWave = { real: $real, imag: $imag, volume: $volume, dur: $dur, freq: $freq, cents: $cents };

function resetSound () {
	real.reset();
	imag.reset();
}

$:chVal(), $freq,$volume;
function chVal () {
	if (!analyser) { return false; }
	osc.frequency.value = $freq;
	gainNode.gain.value = $volume;
	gainNode.gain.setValueAtTime($cents, ac.currentTime);
}

$:chCustomVal(),$real, $imag;
function chCustomVal () {
	if (!analyser || $real.length<2) { return false; }
	var wave = ac.createPeriodicWave(new Float32Array($real), new Float32Array($imag), { disableNormalization: true });
	osc.setPeriodicWave(wave);
}

var ac = new AudioContext();
let analyser;
// var waveform=[];
var gainNode;
var osc;

function doBasicSound(type = "sine") {
	ac = new AudioContext();
	analyser = ac.createAnalyser();

	osc = ac.createOscillator(); // instantiate an oscillator
	osc.frequency.value = $freq; // Hz
	
	osc.type = type; // this is the default - also square, sawtooth, triangle

	gainNode = ac.createGain();
	osc.connect(gainNode);
	gainNode.connect(ac.destination);
	gainNode.gain.value = $volume;
	gainNode.gain.setValueAtTime($cents, ac.currentTime);


	//osc.connect(ac.destination);
	osc.start(); // start the oscillator
	// draw();
	osc.stop($dur); // stop 2 seconds after the current time
}
let acx = new AudioContext();
function doSound({ real, imag, volume, freq, dur, cents }) {
	ac = new AudioContext();
	analyser = ac.createAnalyser();
	// waveform = new Float32Array(analyser.frequencyBinCount);
	// analyser.getFloatTimeDomainData(waveform);
	// drawOscilloscope();
	//  ac = acx;
	console.log("cjk");
	osc = ac.createOscillator();
	osc.frequency.value = freq; // Hz
	// var real = real; //getContext("real");//new Float32Array(2);
	// var imag = imag; //getContext("imag");//new Float32Array(2);
	// real.fill(1);
	// imag.fill(1);
	// real[0] = 0;
	// imag[0] = 0;
	// real[1] = real1;
	// imag[1] = 0;
	if (real.length<2) { return false; }
	var wave = ac.createPeriodicWave(new Float32Array(real), new Float32Array(imag), { disableNormalization: true });
	osc.setPeriodicWave(wave);

	gainNode = ac.createGain();
	osc.connect(gainNode);
	gainNode.connect(ac.destination);
	gainNode.gain.value = volume;
	gainNode.gain.setValueAtTime(cents, ac.currentTime);

	//osc.connect(ac.destination);
	osc.start();
	// draw();
	osc.stop(dur);
}


function waveSetName (i) {
	const defName = $wavesArr[i].name||"";
	const name=prompt("Edytuj nazwę dźwięku", defName);
	if (name===null) { return false; }
	setName(name, i);
}

async function setWave(wave) {
	// arrLength = wave.real.length;
	await tick();
	$real = [...wave.real];
	$imag = [...wave.imag];
	$volume = wave.volume;
	$dur = wave.dur;
	$freq = wave.freq;
	$cents = wave.cents;
}


</script>

<CanvasWave bind:analyser bind:gainNode/>

<div style="display:flex; gap: 40px;">
	<div style="flex-shrink: 0;">
		<div id="basic-types-buttons">
			<button on:click={()=>doBasicSound("sine")}>Do Sine</button>
			<button on:click={()=>doBasicSound("square")}>Do Square</button>
			<button on:click={()=>doBasicSound("sawtooth")}>Do Sawtooth</button>
			<button on:click={()=>doBasicSound("triangle")}>Do Triangle</button>
		</div>
		<div id="panel">
		<div class="factor">
			<input id="volume" type="range" bind:value={$volume} step="0.01" min="0.0" max="1">
			<label for="volume">{Math.floor($volume*100)}% volume</label>
		</div>
		<div class="factor">
			<input id="dur" type="range" bind:value={$dur} step="0.1" min="0.1" max="4">
			<label for="dur">{$dur} sec</label>
		</div>
		<div class="factor">
			<input id="freq" type="range" bind:value={$freq} step="1" min="20" max="20000">
			<input type="number" size="5" bind:value={$freq} step="1" min="20" max="20000"> Hz
		</div>
		<div class="factor">
			<input id="$cents" type="range" bind:value={$cents} min="0" step="0.1" max="1">
			<input type="number" size="5" bind:value={$cents} min="0" step="0.1" max="1"> gain</div>
		</div>
	</div>
	<div style="overflow:hidden;">
		<div id="custom-types-buttons">
			<button
				style="margin-right:10px;"
				title={keys}
				on:mouseup={bt(0, ()=>doSound(aWave) )}
				on:mouseup={bt(1, ()=>saveSound(aWave) )}
				on:mouseup|preventDefault|stopPropagation={bt(2, ()=>resetSound() )}
				on:contextmenu|preventDefault|stopPropagation
			>
				Do Sound
			</button>
			
			{#each $wavesArr as wave, i}
				<button 
					title={`${wave.volume} volume\n${wave.dur} sec\n${wave.freq} Hz\n${wave.cents} gain\n\nlength: ${wave.real.length}\n\nreal: ${wave.real.join(", ")}\n\nimag: ${wave.imag.join(", ")}`}
					on:mouseup|preventDefault|stopPropagation={bt(0, ()=>doSound(wave))}
					on:mouseup|preventDefault|stopPropagation={bt(1, ()=>setWave(wave))}
					on:mouseup|preventDefault|stopPropagation={btCtrl(1, ()=>deleteSound(i))}
					on:mouseup|preventDefault|stopPropagation={bt(2, ()=>saveSound(aWave,i))}
					on:mouseup|preventDefault|stopPropagation={btCtrl(2, ()=>waveSetName(i))}
					on:contextmenu|preventDefault|stopPropagation
				>Do {wave.name?wave.name:`Cus${i+1}`}</button>
			{/each}
		</div>
		<!-- <input id="real1" type="number" bind:value={real1}>
		<label for="real1">{real1} real1</label> -->
		<AdvancedPanel doSound={doSound} />
		
	</div>
</div>

<style>
	button {
	  background: #ff3e00;
	  color: white;
	  border: none;
	  padding: 8px 12px;
	  border-radius: 2px;
	}
	#basic-types-buttons {
	  display: flex;
	  gap: 10px;
	}
	#panel {
	  display: flex;
	  flex-direction: column;
	  width: 150px;
	  gap: 10px;
	  margin-top: 20px;
	  margin-bottom: 20px;
	  margin-left: auto;
	  margin-right: auto;
	}
	
	.factor {
		display: flex;
	}
	
	#custom-types-buttons {
	  display: flex;
	  gap: 10px;
		flex-flow: wrap;
	}
</style>
