<script>
	
	import PeriodicWave from "./PeriodicWave.svelte";
	import Formula from "./Formula.svelte";
	
	import { real, imag } from "./wave.js"
import { tick } from "svelte";
	// import { volume, dur, freq, cents } from "./wave.js"
	
	// $:aWave = { real: $real, imag: $imag, volume: $volume, dur: $dur, freq: $freq, cents: $cents };
	
	// export let doSound;
	
	let mixLength = 1;
	// let arrLength = $real.length;
	
	
function lengthChange (event) {
	const value = Number(event.target.value);
	
	const oReal = $real;
	const nReal = Array(value).fill(0).map((val,i)=>oReal[i]||val);
	$real = [...nReal];
	
	
	const oImag = $imag;
	const nImag = Array(value).fill(0).map((val,i)=>oImag[i]||val);
	$imag = [...nImag];
}

let length = $real.length;
let usedLength = $real.length;
const handleKeyup = (event) => {
	if (['Enter',"NumpadEnter"].includes(event.code)) {
		lengthChange(event);
		usedLength = $real.length;
	}
}

function rLengthChange (event) {
	lengthChange(event);
	length = $real.length;
	usedLength = $real.length;
}
</script>

<style>
	#pw:not(.is-use) {
		background: red;
	}
</style>

<div style="display: flex; white-space: pre-wrap;">
	<input id="length" type="range" min="2" max="40" value={$real.length} on:input={(event)=>rLengthChange(event)}>
	<input type="number" size="3" min="2" id="pw" class:is-use={length===usedLength} bind:value={length} on:keyup|preventDefault={handleKeyup} > PW
	<div>
		<Formula bind:arr={$real} title="real" />
		<Formula bind:arr={$imag} title="imag - tutaj duży wzór!" />
	</div>
</div>

<div style="display:flex;">
	<div style="flex-shrink:0; flex-direction: column; display: flex;">
		<input id="mixLength" type="range" min="1" max="40" bind:value={mixLength} orient="vertical">
		<input type="number" size="3" min="1" bind:value={mixLength} > Mix
	</div>
	<div style="overflow: hidden;">
		<div style="overflow: scroll;">
		<PeriodicWave bind:real={$real} bind:imag={$imag} />
		</div>
	</div>
</div>
